.MockBrowser{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 10px;
    border-bottom: 1px solid rgb(189, 181, 181);
}
    .MockBrowser nav{
        flex-basis: 110px;
        display: flex;
        justify-content:space-between;
        align-items: center;
    }
    .MockBrowser nav button{
            width: 50px;
            height: 50px;
            line-height: 50px;
         
            border:1px solid rgb(189, 181, 181);
        }
    .MockBrowser .path{
        margin-left: 10px;
        padding-left: 10px;
        height: 50px;
        width: 100%;
        line-height: 50px;
        border:1px solid rgb(189, 181, 181);
        flex:1 0 400px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .MockBrowser .path .example{
            color: rgb(139, 132, 132);
        }
    .MockBrowser .path .inputWrapper{
            width: 100%;
            height: 100%;
        }
    .MockBrowser .path input{
            outline: none;
            font-size: inherit;
            width: 100%;
            height: 100%;
            line-height: 50px;
            vertical-align: top;
            border:none;
            background: inherit;
        }
.primary-header{
    
    display: flex;
}
    .primary-header h3{
        width: 400px;
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    .primary-header nav{
        text-decoration: none;
        height: 50px;
        width: 100%;
        display: flex;
        justify-content: space-around;
        align-items: center;
        border-bottom: 2px solid #ccc;
    }
*{
    margin: 0;
    padding: 0;
}
input,button{
    outline: none;
}
html{
    width: 100%;
    height: 100%;
}
body{
    height: 100%;
    width: 100%;
    background: rgba(131, 128, 128, 0.212);
}
